import React from 'react'
import ringing from '../../Components/Media/ringing.png'

function NotificationButton() {
  return (
   <div className='bg-sky-200 w-12 h-12 flex justify-center items-center rounded-full p-2 shadow-xl border-2 border-white fixed bottom-20 right-10 hover:'>
       <img className='w-10' src={ringing} />
   </div>
  )
}

export default NotificationButton