import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { BiChevronsRight } from 'react-icons/bi'
import WaterPaymentModal from './Modals/WaterPaymentModal'
import WaterDocumentModal from './Modals/WaterDocumentModal'
import axios from 'axios'
import { HEADER } from '../Trade/tradeComponent/TradeApiListFile'
import WaterApiList from '../../Components/ApiList/WaterApiList'


function WaterMainComponent(props) {

    const { api_citizenApplications, api_citizenApprovedApplications, header } = WaterApiList()

    const [openPaymentModal, setOpenPaymentModal] = useState(0)
    const [openDocumentModal, setOpenDocumentModal] = useState(0)
    const [appliedList, setAppliedList] = useState()
    const [approvedApplications, setApprovedApplications] = useState()
    const [paymentData, setPaymentData] = useState()
    const [uploadApplicationId, setUploadApplicationId] = useState()
    const [refetchApplicationList, setRefetchApplicationList] = useState(0)

    const navigate = useNavigate()


    const handlePayBtn = (id, type) => {
        console.log("Clicked PAy")
        setOpenPaymentModal(prev => prev + 1)
        setPaymentData({ 'id': id, 'type': type })
    }

    const handleUploadBtn = (id) => {
        console.log("Clicked Document Upload")
        setOpenDocumentModal(prev => prev + 1)
        setUploadApplicationId(id)
        props.uploadDocId(id)
    }

    useEffect(() => {
        axios.post(api_citizenApprovedApplications, {}, header)
            .then((res) => {
                if (res.data.status == true) {
                    console.log("Approved Application Listst", res.data)
                    setApprovedApplications(res.data.data)
                } else {
                    console.log("Error Approced Application Listst", res)
                }
            })
            .catch((err) => console.log("error ecxewption", err))
    }, [refetchApplicationList])

    useEffect(() => {
        axios.post(api_citizenApplications,{}, header)
            .then((res) => {
                if (res.data.status == true) {
                    console.log("Application Listst", res)
                    setAppliedList(res.data.data)
                } else {
                    console.log("Error its FALSE", res)
                }
            })
            .catch((err) => console.log("error ecxewption", err))
    }, [refetchApplicationList])


    return (
        <>
            <WaterPaymentModal openPaymentModal={openPaymentModal} paymentData={paymentData} refechList={() => setRefetchApplicationList(prev => prev + 1)} />
            {/* <WaterDocumentModal openDocumentModal={openDocumentModal} uploadApplicationId={uploadApplicationId}/> */}

            <div>
                {/* <h1 className='font-semibold text-2xl text-gray-600 capitalize m-5 mb-2'>Manage all your water connections at one place</h1> */}
                <h1 className='px-2 font-semibold mt-0 text-center text-gray-900 font-serif py-2 md:text-2xl uppercase '>Manage all your water connections at one place</h1>

                <p className='border-b border-gray-300 mx-5'></p>
                <div className="float-right mt-2 mr-3"> <button onClick={() => navigate('/water-apply')} className='py-2 mx-2 px-4 inline-block text-center mb-3 rounded leading-5 text-gray-100 bg-sky-500 border border-sky-500 hover:text-white hover:bg-sky-600 hover:ring-0 hover:border-sky-600 focus:bg-sky-600 focus:border-sky-600 focus:outline-none focus:ring-2' >Apply New Connection</button> </div>

                {/* Total Water Connections */}

                <div className='px-2 w-full mt-10'>
                    <div className='text-lg font-sans font-semibold text-gray-700 mx-2 my-2'>All Water Connections</div>

                    {approvedApplications?.map((item) => (


                        <div className='bg-white shadow-lg pb-2 oveflow-hidden mb-2 rounded-lg relative'>
                            <div className='w-full py-8 rounded-lg  px-4 md:flex'>
                                <span className='px-3 py-1 bg-green-200 border border-green-300 text-gray-700 text-xs font-semibold absolute rounded-tl-md rounded-br-md top-0 left-0 shadow-sm'>Active Water Connection </span>

                                <div className='w-full flex space-x-2 mt-3'>
                                    <div>
                                        <div className='font-semibold text-gray-700'>{item?.consumer_no}</div>
                                        <div className='text-sm text-gray-600 font-serif'>Connection No.</div>
                                    </div>
                                    <div className='pl-4'>
                                        <div className='font-semibold text-gray-700'>{item?.applicant_name}</div>
                                        <div className='text-sm text-gray-600 font-serif'>Owner Name</div>
                                    </div>
                                    <div className='pl-4'>
                                        <div className='font-semibold text-gray-700'>{item?.ward_name}</div>
                                        <div className='text-sm text-gray-600 font-serif'>Ward No</div>
                                    </div>
                                    <div className='pl-4'>
                                        <div className='font-semibold text-gray-700'>{item?.ulb_name}</div>
                                        <div className='text-sm text-gray-600 font-serif'>ULB Name</div>
                                    </div>
                                    <div className='pl-4'>
                                        <div className='font-semibold text-gray-700'>{item?.address}</div>
                                        <div className='text-sm text-gray-600 font-serif'>Address</div>
                                    </div>

                                </div>
                                <div className='float-right'>
                                    <div className='flex'>
                                        <button onClick={()=>(navigate(`/water/viewApplication/${item?.consumer_no}`))} className='border text-sm bg-amber-400 hover:bg-amber-500 mx-2 my-2 px-3 py-2 rounded-md shadow-lg'> View Application</button>
                                        <button className='border text-sm bg-green-400 hover:bg-green-500 mx-2 my-2 px-3 py-2 rounded-md shadow-lg'> View Demand</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
                {/* Total Water Connections END */}



                {/* Applied Water Applications */}

                <div className='px-2 w-full mt-10'>
                    <div className='text-lg font-sans font-semibold text-gray-700 mx-2 my-2'>Applied Water Applications</div>

                    {appliedList?.map((item, i) => (
                        <div className='bg-white shadow-lg pb-2 oveflow-hidden mb-2 rounded-lg relative'>
                            {/* application card */}
                            <div className='w-full py-4 rounded-lg  px-4 md:flex'>
                                <span className='px-3 py-1 bg-yellow-200 border border-yellow-300 text-gray-700 text-xs font-semibold absolute rounded-tl-md rounded-br-md top-0 left-0 shadow-sm'>Pending Application </span>

                                <div className='w-full flex space-x-2 mt-3'>
                                    <div>
                                        <div className='font-semibold text-gray-700'>{item?.application_no}</div>
                                        <div className='text-xs text-gray-600 font-serif'>Application No.</div>
                                        <div className='text-xs text-gray-600 font-serif'>Ward No (ID) - {item?.ward_name} ({item?.ward_id})</div>
                                    </div>
                                    <div className='pl-4'>
                                        <div className='font-semibold text-gray-700'>₹ {item?.amount}</div>
                                        <div className='text-xs text-gray-600 font-serif'>Total Tax</div>
                                    </div>

                                    <p className='border-r border-gray-600 md:pl-10'></p>

                                    <div className='w-full md:w-1/4 pl-2 text-xs'>
                                        <div className='font-semibold font-sans'>Actions</div>
                                        <div className='text-sm text-amber-500'> Document Upload Pending</div>
                                        {item?.payment_status ? <div className='text-sm text-green-500'> Payment Completed</div> : <div className='text-sm text-amber-500'> Waiting for Payment</div>}
                                    </div>

                                    <div className='block'>
                                        <button className='border text-sm bg-blue-400 hover:bg-blue-500 mx-2 py-2 px-3 text-white font-sans font-normal rounded-md shadow-lg'> View Application</button>
                                        <button onClick={() => handleUploadBtn(item.id)} className='border text-sm bg-indigo-400 hover:bg-indigo-500 py-2 mx-2 px-3 rounded-md text-white font-sans font-normal shadow-lg'> Upload Document</button>
                                        {!item?.payment_status && <button onClick={() => handlePayBtn(item.id, item.type)} className='border text-sm py-2 text-white font-sans font-normal bg-cyan-400 hover:bg-cyan-500 mx-2 px-3 rounded-md shadow-lg'> Pay Now</button>}
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
                {/* Applied Water Applications END */}

            </div>
        </>
    )
}

export default WaterMainComponent
