import React, { useState } from 'react'
import WaterDocumentUpload from './WaterDocumentUpload'
import WaterMainComponent from './WaterMainComponent'
import WaterNotificationSide from './WaterNotificationSide'

function WaterView() {

    const [uploadDocBtnId, setUploadDocBtnId] = useState()

    const uploadDocId = (id) => {
        console.log("uploadDocId", id)
        setUploadDocBtnId(id)
    }

    return (
        <>
            <div className='grid grid-cols-12 my-10'>
                <div className='col-span-9 border rounded-md bg-blue-50'>
                    {!uploadDocBtnId && < WaterMainComponent uploadDocId={uploadDocId} />}
                    {uploadDocBtnId && <WaterDocumentUpload uploadDocBtnId={uploadDocBtnId} back={()=>setUploadDocBtnId()} />}
                </div>
                <div className='col-span-3 border rounded-md ml-3 bg-blue-50'> <WaterNotificationSide /> </div>
            </div>
        </>

    )
}

export default WaterView

